html {
  /* 16px * 62.5% = 10px */
  font-size: 62.5%; 
}

body {
  /* 10px * 1.6rem = 16px */
  font-size: 1.6rem; 
  font-family: 'Poiret One', cursive;
  font-family: 'Hind Siliguri', sans-serif;
}

* {
  margin: 0;
  padding: 0;
}

img {
  /* height:auto will ensure the image's dimensions are consistent throughout its parent's varying width */
  width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover{
  outline: none;
}

ul, 
ol, 
li {
  list-style: none;
}

input, 
button {
  border: none;
  box-shadow: none;
  background: none;
}

button {
  cursor: pointer;
}

.wrapper {
  max-width: 1200px;
  width: 80%;
  margin: 0 auto;
}

.gameShell {
  background-color: #000000;
  height: 100vh;
  position: relative;
}

