.preloader {
  height: 100vh;
  width: 100vw;
  position: absolute;
  z-index: 100;
  background: black;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.preloader img {
  position: absolute;
  /* left: -10%; */
  width: 20%;
  max-width: 150px;
  -webkit-animation: 800ms ease-out infinite loadermation;
          animation: 800ms ease-out infinite loadermation;
  border-bottom: #50C17F;
}

@-webkit-keyframes loadermation{
  0% {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
  }
}

@keyframes loadermation{
  0% {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
  }
  50% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
  }
}



main {
  /* I set an explicit height since the app will fit within the viewheight */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.head {
  text-align: center;
  font-family: 'Poiret One', cursive;
  color: white;
  padding-top: 10%;
}

h1 {
  margin: 0;
}

.headTitle {
  font-size: 5.5rem;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.headImageContainer {
  width: 20%;
  margin: 0 auto;
}

.initialForm {
  display: flex;
  flex-direction: column;
}

input {
  background-color: white;
  width: 400px;
  height: 30px; 
  margin: 5px auto;
  padding: 2%;
}

.formButton {
  width: 200px;
  height:50px;
  background-color: #82BD49;
  color: white;
  margin: 5px auto;
}





.maze {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

canvas {
  margin: 20px;
  width: 100%;
}

.mazeContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.canvasContainer {
  margin: 0 auto;
}

