/* GoogleFonts import */

.leaderboardHeader {
  font-family: 'Poiret One', cursive;
  letter-spacing: 2px;
  margin-bottom: 20px;
}

.leaderboardContainer {
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  width: 65%;
  background-color: rgba(0, 0, 0, 0.8);
  color: rgb(255, 255, 255);
  text-align: center;
  padding: 30px;
}

.leaderboardContainer button {
  position: absolute;
  left: 90%;
  top: 5px;
  color: rgb(255, 255, 255);
}

.leaderboardContainer li {
  font-size: 1.7rem;
  margin-bottom: 5px;
}

.resultContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 60%;
  margin: 0 auto;
}

.resultContent {
  margin-top: 30px;
  padding: 20px;
  background: rgb(59, 57, 57);
  border-radius: 10px;
  color: rgb(255, 255, 255);
}

.resultUserName {
  font-size: 3rem;
  margin-bottom: 15px;
}

.resultAdvice {
  font-size: 2.1rem;
  margin-bottom: 15px;
}

.resultContainer button {
  margin: 10px;
  background-color: rgba(124, 140, 153, .5);
  padding: 10px 20px;
}


